import { Injectable } from '@angular/core';

const auths = 'AuthAuthorities';
const username = 'AuthUsername';
const key = 'AuthToken';


@Injectable({
  providedIn: 'root'
})
export class TokenService {

  private roles: Array<string> = [];

  constructor() {

  }

  //Guarda el nombre de usuario del usuario en sesin
  saveUser(username) {
    window.sessionStorage.removeItem(username);
    window.sessionStorage.setItem(username, username);
  }

  //Guarda el token del usuario en sesin
  save(token) {
    window.sessionStorage.removeItem(key);
    window.sessionStorage.setItem(key, token);
  }

  //Guarda el rol del usuario en sesin
  saveAuths(authorities) {
    window.sessionStorage.removeItem(auths);
    window.sessionStorage.setItem(auths, JSON.stringify(authorities));
  }

  getUser() {
    return sessionStorage.getItem(username);
  }

  //Devuelve el token del usuario en sesin
  getToken() {
    return sessionStorage.getItem(key);
  }

  //Cierra sesin
  out() {
    window.sessionStorage.clear();
  }

  //Devuelve una lista de roles del usuario en sesin
  getAuthorities(){
    this.roles = [];

    if (sessionStorage.getItem(key)) {

      JSON.parse(sessionStorage.getItem(auths)).forEach(auth => {

        this.roles.push(auth.authority);

      });
    }

    return this.roles;
  }
}
