import {Component, OnInit, Input } from '@angular/core';
import { ExperimentService } from '../experiment.service'
import { TranslateService } from '@ngx-translate/core';
import { Investigator } from '../../investigator/model/investigator';
import { PetitionService } from '../../petition/petition.service'
import { Petition } from '../../petition/model/petition';
import { Message } from 'primeng/api';
import {MessageService} from 'primeng/api';


@Component({
  selector: 'investigators-associated',
  templateUrl: './investigatorAssociated.component.html',
  styleUrls: ['./investigatorAssociated.component.css' , '../../shared/common-style.css'],
  providers: [MessageService]

})
export class InvestigatorsAssociatedComponent implements OnInit{ 

    //Experimento en cuestin
    @Input("idExperiment") idExperiment : number;

    //Lista de investigadores que pueden ser asociados al experimento en cuestin
    investigatorsNotAssociated : Investigator[] = []

    //Sirve para mostrar/ocultar la lista de investigadores no asociados al experimento
    displayModalNewAssociation: boolean;

    //Sirve para mostrar/ocultar la ventana modal de confirmacin de asociacin
    displayModalConfirmAssociation: boolean;

    //Identificador del investigador que se ha elegido para llevar a cabo la asociacin
    private idInvestigator : number;

    //Identifica si ha seleccionado ser gestor o no
    gestor: string;

    //Alertas
    msgs: Message[];

    //Lista de investigadores
    investigators: Investigator[] = [];

    //Columnas de la tabla de investigadores
    cols: any[];

    constructor(
        private experimentService: ExperimentService,
        private translate: TranslateService,
        private petitionService: PetitionService,
        private messageService: MessageService,
    ) { }

    ngOnInit() {
        this.experimentService.getInvestigatorsOfExperiment(this.idExperiment)
            .then(investigators => this.investigators = investigators);

        this.loadInvestigatorsNotAssociated();

        this.cols = [
            { field: 'name', header: 'name', width: '15%' },
            { field: 'mail', header: 'mail', width: '40%' },
            { field: 'username', header: 'username', width: '20%'},
            { field: 'manager', header: 'rol', width: '10%' }
        ];
        
    }

    private loadInvestigatorsNotAssociated(){
        this.experimentService.getInvestigatorsNotAssociatedAnExperiment(this.idExperiment)
            .then(investigatorsNotAssociated => this.investigatorsNotAssociated = investigatorsNotAssociated);
    }

    /**
     * Muestra la ventana modal de aadir investigadores al experimento
     */
    public showModalInvestigators(){
        this.displayModalNewAssociation = true;
    }

    /**
     * Muestra la ventana de confirmacin de envo de envo de asociacin
     * @param id identificar del investigador que se va a asociar
     */
    public showModalConfirmAssociation(id: number){
        this.idInvestigator = id;
        this.displayModalConfirmAssociation = true;
    }

    /**
     * Cierra la ventana de confirmacin de envo de asociacin
     */
    public cancelModalConfirmAssociation(){
        this.displayModalConfirmAssociation = false;
    }

    /**
     * Confirma la asociacin 
     */
    public confirm(){
        //Se lleva a cabo la creacin de la peticin correspondiente
        var resObs = this.petitionService.register(this.createPetition());
        resObs.subscribe(
            res => {
                //Se cierra la ventana modal de confirmacin
                this.cancelModalConfirmAssociation();
                //Se establece como indefined el atributo gestor
                this.gestor = undefined
                //Se muestra un mensaje en pantalla indicando que la peticin ha sido creada correctamente
                this.translate.get('experiment.investigatorsAssociated.associationSuccess').subscribe((data:any)=> {
                    this.show('success', 'Success', data);
                });
                //Se recarga los investigadores que pueden ser asociados al experimento
                this.loadInvestigatorsNotAssociated();
            },
            err => {
                this.translate.get('experiment.investigatorsAssociated.associationErr').subscribe((data:any)=> {
                    this.show('error', 'Error', data);
                });
            }
        );
    }

    /**
     * Crea un objeto peticin con los datos de la peticin correspondiente
     */
    private createPetition(): Petition{
        let petition = new Petition();
        petition.idExperiment = this.idExperiment;
        petition.idInvestigator = this.idInvestigator;
        if(this.gestor !== undefined){
            petition.manager = true;
        }
        else{
            petition.manager = false;
        }
        return petition;
    }

    /**
     * Mostrar alertas en pantalla
     */
    public show(sever: string, summ: string, info: string) {
        this.messageService.add({severity: sever, summary: summ, detail: info});
    }

}
