import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest } from '@angular/common/http';

import { TokenService } from './token.service';

const header = 'Authorization';

@Injectable()
export class Interceptor implements HttpInterceptor {

    constructor(
        private token: TokenService
    ) { 

    }

    /**
     * Mtodo de la clase HttpInterceptor encargado de interceptar los tokens
     * @param req 
     * @param next 
     */
    intercept(req: HttpRequest<any>, next: HttpHandler) {
        var req2 = req;

        const token = this.token.getToken();

        if (token != null) {
            req2 = req.clone({headers:req.headers.set(header, 'Bearer ' + token) });
        }
        
        return next.handle(req2);
    }
}
//Exporta el interceptor que se encarga de interceptar el envo de tokens entre el cliente y el servidor
export const httpInterceptorProviders = [
    { provide: HTTP_INTERCEPTORS, useClass: Interceptor, multi: true }
];